.myOrdersPage {
  width: 100vw;
  max-width: 100%;
  padding:0vmax 8vmax;
  box-sizing: border-box;
  background-color: rgb(235, 235, 235);
  /* position: fixed; */
  top: 0;
  left: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

#myOrdersHeading {
  text-align: center;
  font: 1.8vmax "Roboto Slab";
  padding: 0.5vmax;
  box-sizing: border-box;
  color: #303030;
  transition: all 0.5s;
  background-color: rgb(255, 255, 255);
}

.myOrdersTable {
  background-color: white;
}

.myOrdersTable div {
  font: 300 1vmax "Roboto Slab";
  color: rgba(0, 0, 0, 0.678);
  border: none;
}

.myOrdersTable a {
  color: rgba(0, 0, 0, 0.527);
  transition: all 0.5s;
}

.myOrdersTable a:hover {
  color: #800039;
}

.MuiDataGrid-columnHeader {
  background-color: #800039;
  padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
  color: rgb(255, 255, 255);
  font: 500 1.1vmax "Roboto Slab" !important;
}

.MuiDataGrid-iconSeparator {
  display: none !important;
}

@media screen and (max-width: 600px) {
  .myOrdersPage {
    padding: 0;
    height: 93vh;
  }

  #myOrdersHeading {
    font: 400 2.2vmax "Roboto Slab";
    padding: 4vw;
  }

  .myOrdersTable div {
    font: 300 4vw "Roboto Slab";
  }

  .MuiDataGrid-columnHeader {
    padding: 20px !important;
  }

  .MuiDataGrid-columnHeader div {
    font: 500 5vw "Roboto Slab" !important;
  }
}
